.storage-list {
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    line-height: 14px;
    color: #040E34;

    .com-card {
        .com-top {
            display: flex;
            justify-content: space-between;
            padding: .26rem .3rem;

            .top-title {
                font-size: .18rem;
            }

            .top-btn {
                width: .88rem;
                height: .4rem;
                background: #5C5EE4;
                opacity: 1;
                border-radius: 4px;
                color: #FFFFFF;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
            }
        }
    }

    // 挂载弹窗
    .create-snapshot {
        .top {
            .title {
                font-size: 18px;
                font-weight: 500;
                color: #040E34;
            }
        }

        .main {
            // margin-top: 40px;

            .form {
                .row {
                    margin-top: 24px;

                    .ant-input {
                        width: 95%;
                    }

                    .ant-select {
                        width: 90%;
                        // background: #F2F2F7;
                    }

                    // .ant-select-selector {
                    //     // background: #F2F2F7;
                    // }

                    .label {
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-size: 13px;
                        color: #040E34;
                    }
                }
            }
        }

        .footer {
            display: flex;
            flex-direction: row-reverse;
            margin-top: 48px;

            .cancel-btn {
                height: 40px;
                background: #F2F2F7;
                border-radius: 4px;
                border: none;
                color: #525666;
            }

            .sub-btn {
                height: 40px;
                background: #696AE6;
                border-radius: 4px;
                color: #FFFFFF;
                margin-right: 8px;
            }
        }
    }

}

.refund-modal {
    width: 6.4rem !important;
    font-family: 'Source Han Sans CN';
  
    .ant-modal-body {
      padding: .4rem .3rem;
    }
  
    .modal-title {
      font-size: .18rem;
      color: #040E34;
      line-height: 1;
      margin-bottom: .3rem;
    }
  
    .config-item {
      margin-bottom: .2rem;
      display: flex;
  
      &:first-child {
        .row-r {
          padding: .25rem .2rem;
          font-size: .13rem;
          color: #9696A3;
        }
  
        .l-tit {
          margin-right: .2rem;
        }
  
        .item {
          display: flex;
          line-height: .14rem;
          margin-bottom: .2rem;
  
          &:last-child {
            margin-bottom: 0;
          }
  
          p {
            margin-bottom: 0;
          }
        }
  
        .r-text {
          p {
            margin-bottom: .1rem;
  
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
      }
  
    }
  
    .row-l {
      line-height: .4rem;
      font-size: .14rem;
      color: #040E34;
      min-width: .8rem;
    }
  
    .row-r {
      border-radius: .04rem;
      background: #F2F2F7;
      flex: 1;
    }
  
    textarea,
    .ant-select-selector {
      background: #F2F2F7 !important;
      resize: none;
      border-color: #F2F2F7 !important;
    }
  
    .money {
      align-items: center;
  
      .row-l,
      .row-r {
        line-height: 1;
      }
    }
  
    .modal-footer {
      text-align: center;
  
      .ant-btn {
        line-height: .4rem;
        height: .4rem;
        min-width: .8rem;
        padding: 0 .16rem;
        border-radius: .04rem;
        margin: 0 .04rem;
      }
  
      .sure {
        background: #5C5EE4;
        color: #fff;
        border-color: #5C5EE4;
      }
  
      .cancel {
        border-color: #D9DBE1;
        color: #525666;
      }
    }
  
    .ant-select-selection-item {
      font-size: .14rem;
      color: #040E34;
    }
  }
  
@primary-color: #5C5EE4;